// 
export function deepClone(target) {
  // 
  let result;
  // 
  if (typeof target === 'object') {
    // 
    if (Array.isArray(target)) {
      result = []; // result
      for (let i in target) {
        // 
        result.push(deepClone(target[i]))
      }
      // nullnull
    } else if (target === null) {
      result = null;
      // RegExp    
    } else if (target.constructor === RegExp) {
      result = target;
    } else {
      // for in
      result = {};
      for (let i in target) {
        result[i] = deepClone(target[i]);
      }
    }
    // 
  } else {
    result = target;
  }
  // 
  return result;
}

