export function getNowFormatDate() {
  var date = new Date();
  var seperator1 = "-";
  var year = date.getFullYear();
  var month = date.getMonth() + 1;
  var strDate = date.getDate();
  var hours = date.getHours();
  var seconds = date.getSeconds();
  var minutes = date.getMinutes();
  if (month >= 1 && month <= 9) {
    month = "0" + month;
  }
  if (strDate >= 0 && strDate <= 9) {
    strDate = "0" + strDate;
  }
  if (hours <= 9) {
    hours = "0" + hours
  }
  if (minutes <= 9) {
    minutes = "0" + minutes
  }
  if (seconds <= 9) {
    seconds = "0" + seconds
  }


  var currentdate = year + seperator1 + month + seperator1 + strDate + " " + hours + ":" + minutes + ":" + seconds;
  return currentdate;
}

export function stringToArray(arr) {
  return arr && arr.length > 0 ? arr.split(',') : null
}

export function setCookie (key, value, day) {
  let expires = day * 86400 * 1000  //  ms
  let date = new Date( + new Date() + expires) // 
  document.cookie = `${key}=${value};expires=${date.toUTCString()}`
}

export function getCookie(cname){
  var name = cname + "=";
  var ca = document.cookie.split(';');
  for(var i=0; i<ca.length; i++) 
  {
    var c = ca[i].trim();
    if (c.indexOf(name)===0) return c.substring(name.length,c.length);
  }
  return "";
}

//
export function downFile(res, fileName) {
  const blob = new Blob( [res], {type: "application/x-xls;charset=utf-8"} )
  // URL
  const url = window.URL || window.webkitURL || window.moxURL
  // 
  const downloadHref = url.createObjectURL(blob)
  // a
  let downloadLink = document.createElement("a")
  downloadLink.href = downloadHref
  downloadLink.download = fileName
  // 
  downloadLink.click()
  window.URL.revokeObjectURL(url);
}

